/* .items > .container > .row{
  padding: 2px;
} */

.items > .container .col {
  padding: 2px;
}

.items > .container .row:first-child .col:first-child {
  min-width: 60% !important;
}

.items .col > .card {
  height: calc((100vh - 156px) / 3);
  overflow: hidden;
}

.items .card-img {
  height: 100%;
  object-fit: cover;
}

