.items .card-overlay {
  position: absolute;
  bottom: -22px;
  left: 0;
  right: 0;

  overflow: hidden;
  width: 100%;
  height: 76px;
  transition: 0.5s ease;
  padding: 1.25rem;
  padding-top: 0.95rem;

  background-color: #0c3763e3;
}

.items .card:hover .card-overlay {
  height: 100%;
  padding: 1.25rem;
  bottom: 0;
}

.items .rating {
  margin-top: 1rem;
}
